.form {
  width: 300px;
  padding: 50px;
  margin-left: auto;
  margin-right: auto;
  border-radius: 7px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  background-color: rgb(234, 241, 163);
}

.form input {
  display: block;
  outline: none;
  margin-top: 10px;
  margin-bottom: 15px;
  padding: 7px 15px;
  border-radius: 5px;
  border: 1px solid rgb(234, 241, 163);
  text-align: left;
}
.form button {
  background-color: rgb(50, 167, 196);
  margin: 5px;

  padding: 7px 15px;
  border-radius: 5px;

  text-align: center;
}

.form li {
  display: block;
  margin: 5px;
}
